@import './header.css';
@import './font.css';

*,
*::before,
*::after {
    box-sizing: border-box;
}

body {
    min-height: 100vh;
    min-width: 320px;
    scroll-behavior: smooth;
    text-rendering: optimizeSpeed;
    line-height: 1.5;
    font-family: 'Mono';
}

body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
figcaption,
blockquote,
dl,
dd {
    margin: 0;
}

ul:not(:last-child),
ol:not(:last-child) {
    margin-bottom: 35px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    line-height: 1.65;
}

p {
    font-size: 1.125rem;
    line-height: 2;
}

h1 {
    margin-bottom: 45px;
    font-size: 3rem;
}

h2 {
    margin: 45px 0;
    font-size: 1.625rem;
}

h3,
h4 {
    margin: 15px 0;
    font-size: 1.5rem;
}

p:not(:last-child) {
    margin-bottom: 35px;
}

a {
    text-decoration: none;
    /* color: var(--link);
    box-shadow: 0 1px 0 0 var(--link-border); */
    transition: all 0.3s;
}

a:focus,
a:active {
    /* color: var(--link); */
    box-shadow: none;
    /* outline: var(--menu-link-color-active) auto 5px; */
}

.gatsby-resp-image-link {
    box-shadow: none;
}

@media (hover: hover) {
    a:hover {
        /* color: var(--link); */
        box-shadow: none;
    }
}

img {
    display: block;
    max-width: 100%;
    margin: auto;
}

input,
button,
textarea,
select {
    font: inherit;
}

button:not(:disabled),
[type='button']:not(:disabled),
[type='reset']:not(:disabled),
[type='submit']:not(:disabled) {
    cursor: pointer;
}

.visuallyhidden {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

code {
    font-size: 1rem;
    overflow-wrap: break-word;
    word-wrap: break-word;
    padding: 2px 5px 3px;
    border-radius: 3px;
    /* background: var(--mine-shaft); */
}

pre {
    clear: both;
    background: rgba(32, 28, 41, 0.75);
    padding: 0;
    word-break: normal;
    hyphens: none;
    position: relative;
    border-radius: 8px;
    min-width: 100px;
    max-width: 100%;
    overflow: hidden;
}

pre code {
    font-size: 1rem;
    line-height: 1.75;
    background: 0 0;
    padding: 30px;
    white-space: pre;
    display: block;
    overflow-x: scroll;
    max-width: 100%;
    min-width: 100px;
}

.gatsby-highlight {
    margin: 0 -40px 35px;
}

figcaption {
    font-style: italic;
    text-align: center;
    margin: -10px 0 35px;
}

@media (prefers-reduced-motion: reduce) {
    * {
        animation-duration: 0.01ms !important;
        animation-iteration-count: 1 !important;
        transition-duration: 0.01ms !important;
        scroll-behavior: auto !important;
    }
}
